@import "colors.scss";
@import "constants.scss";
@import "fonts.scss";

.header {
	background-color: $card-bg;

	ul {
		list-style: none;
	}
}

.topnav {
	align-items: flex-start;
	background-color: rgba(0, 0, 0, 0);
	display: flex;
	font-family: $font-family-p;
	justify-content: space-between;
	margin: 0;
	min-height: 8vh;
	padding: 0.7rem 2rem 1rem 2rem;
	transition: 400s;

	.logo {
		margin: 10px;
		display: inline-block;

		img {
			position: relative;
			text-align: center;
		}
	}

	.flex_group {
		align-items: center;
		display: inline-flex;
		height: 100%;
	}

	.nav-links {
		display: flex;
		transition: 0.3s;

		li {
			list-style: none;
			padding: 10px;
			transition: 0.3s;
		}
	}
}

@media only screen and (max-width: $mobile-max-size) {
	.topnav {
		padding: 0.7rem 1rem 1rem 1rem;

		.flex_group {
			margin: 0;
			padding: 0;
		}
	}
}

.header-link {
	color: $fg-1;
	font-size: 1.5rem;
	font-weight: bold;
	margin: 0;
	padding: 0;

	a {
		color: inherit;
		transition: 0.3s;
		text-decoration: none;

		&:hover {
			color: $accent-2;
		}

		&:visited:hover {
			color: $accent-2;
			text-decoration: inherit;
		}
	}
}

.home-icon {
	border-radius: 9999px;
	margin: 1rem 1rem 1rem 1rem;
	height: 3rem;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

@media (prefers-reduced-motion: no-preference) {
	.home-icon:hover {
		animation: home-icon-spin infinite 3s cubic-bezier(0.25, 0.46, 0.65, 1);
	}
}

@keyframes home-icon-spin {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}